<template>
  <div class="top">
    <el-row class="fitersLine">
      <el-col :span="24">
        <el-form :inline="true">
          <el-select size="small" v-model="queryParams.jobGroup" @change="groupChange" placeholder="请选择执行器">
            <el-option
              v-for="item in executors"
              :key="item.id"
              :label="item.title"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select size="small" v-model="queryParams.jobId" placeholder="请选择作业">
            <el-option
              v-for="item in jobs"
              :key="item.id"
              :label="item.jobDesc"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select size="small" v-model="queryParams.logStatus" placeholder="请选择日志状态">
            <el-option
              v-for="item in statusEnum"
              :key="item.code"
              :label="item.msg"
              :value="item.code">
            </el-option>
          </el-select>
          <el-form-item>
            <el-date-picker
              size="small"
              v-model="queryParams.filterTime"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="getList">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" @click="clearFilterParam">清除</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-table
      ref='list'
      :key='tableKey'
      :data="list"
      v-loading="listLoading"
      element-loading-text="数据加载中，请稍后"
      border
      fit
      highlight-current-row
      select-on-indeterminate
      style="width: 100%">

      <el-table-column align="center" label="任务ID" min-width="60">
        <template scope="scope">
          <span class="link-type">{{scope.row.jobId}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="任务名称" min-width="80">
        <template scope="scope">
          <span class="link-type">{{scope.row.jobName}}</span>
        </template>
      </el-table-column>
      <el-table-column align="center" label="调度时间" min-width="120">
        <template scope="scope">
          <span class="link-type">{{scope.row.triggerTime|dateformat('YYYY-MM-DD HH:mm:ss')}}</span>
        </template>
      </el-table-column>

      <el-table-column min-width="60" align="center" label="调度结果" show-overflow-tooltip>
        <template scope="scope">
          <span v-html="resultDesc(scope.row.triggerCode)"></span>
        </template>
      </el-table-column>

      <el-table-column min-width="100" align="center" label="执行时间" show-overflow-tooltip>
        <template scope="scope">
          <span>{{scope.row.handleTime===null?'':scope.row.handleTime|dateformat('YYYY-MM-DD HH:mm:ss')}}</span>
        </template>
      </el-table-column>

      <el-table-column min-width="60" align="center" label="执行结果" show-overflow-tooltip>
        <template scope="scope">
          <span v-html="resultDesc(scope.row.handleCode)"></span>
        </template>
      </el-table-column>

      <el-table-column align="center" label="操作" min-width="80">
        <template scope="scope">
          <el-button size="small" type="primary" @click="logDetail(scope.row)">查看详情</el-button>
        </template>
      </el-table-column>
    </el-table>

    <div v-show="!listLoading" class="pagination-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page.sync="queryParams.start"
                     :page-sizes="[10,20,30,50]" :page-size="queryParams.length"
                     layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
    <el-dialog title="日志详情" :visible.sync="dialogFormVisible" center>
      <el-form :model="detailForm">
        <el-form-item label="调度备注：" label-width="80px" prop="userName">
          <span v-html="detailForm.triggerMsg"></span>
        </el-form-item>
        <el-form-item label="执行备注：" label-width="80px" prop="realName">
          <span>{{detailForm.executorParam}}</span>
        </el-form-item>
        <el-form-item label="执行日志：" label-width="80px" prop="phone">
          <span>{{detailForm.handleMsg}}</span>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {getToken} from '@/utils/auth' // 验权
  import {getGroupListAll} from "../../api/group";
  import {getLogsList} from "../../api/logs";
  import {getJobsByGroup} from "../../api/jobs";

  export default {
    name: 'list',
    props: {},
    components: {},
    data() {
      return {
        detailForm: {triggerMsg: '', executorParam: '', handleMsg: ''},
        statusEnum: [{code: 1, msg: '成功'}, {code: 2, msg: '失败'}, {code: 3, msg: '进行中'}],
        executors: [],
        jobs: [],
        dialogStatus: 'create',
        dialogFormVisible: false,
        headers: {},
        modifyData: '',
        list: null,
        total: null,
        listLoading: true,
        queryParams: {
          jobGroup: null,
          jobId: null,
          start: 1,
          length: 10,
          logStatus: null,
          filterTime: [] //[new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
        },
        tableKey: 0,
        pageBoolean: {
          delColor: 'primary',
          delText: '批量删除',
          addColor: 'success'
        },
      }
    },
    created() {
    },
    mounted() {
      this.headers = {accessToken: getToken()}
      this.getList()
      this.getExecutors()
    },
    watch: {},
    methods: {
      getList() {
        getLogsList(this.queryParams).then(res => {
          const {code, content: {data, recordsTotal}} = res
          this.listLoading = false
          if (code === 200) {
            this.list = data
            this.total = recordsTotal
            if (this.total === 0) {
              this.$message({
                message: '检索结果为空',
                type: 'warning'
              })
            }
          } else {
            this.$message(res.msg)
          }
        }).catch(function (err) {
          console.log(err)
        })
      },
      getExecutors() { //查询执行器
        getGroupListAll().then(res => {
          const {code, content} = res
          if (code === 200) {
            this.executors = content
          }
        }).catch(function (err) {
          console.log(err.message)
        })
      },
      groupChange() {
        this.queryParams.jobId = null
        if (this.queryParams.jobGroup !== null) {
          getJobsByGroup(this.queryParams.jobGroup).then(res => {
            const {code, content} = res
            if (res.code === 200) {
              this.jobs = content
            }
          }).catch(function (err) {
            console.log(err.message)
          })
        }
      },
      logDetail(row) { // 日志详情
        this.dialogFormVisible = true
        this.detailForm = row
      },
      resultDesc(resultCode) {
        if (resultCode === 200) {
          return '<span style="color: green">成功</span>'
        } else if (resultCode === 0) {
          return '<span style="color: dodgerblue"></span>'
        } else {
          return '<span style="color: red">失败</span>'
        }
      },
      clearFilterParam() {
        this.queryParams.jobId = null
        this.queryParams.jobGroup = null
        this.queryParams.logStatus = null
        this.queryParams.filterTime = []
      },
      handleFilter() {
        this.queryParams.start = 1
        this.getList()
      },
      handleSizeChange(val) {
        this.queryParams.length = val
        this.getList()
      },
      handleCurrentChange(val) {
        this.queryParams.start = val
        this.getList()
      },
      handleRefresh() {
        this.list = null
        this.total = null
        this.queryParams.start = 1
        this.getList()
      }
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  .app-container {
    padding: 20px;
  }

  .pagination-container {
    margin-top: 30px;
  }
</style>
<style rel="stylesheet/scss" lang="scss">
  .top {
    padding: 10px;
  }

  .el-dialog__header {
    padding-bottom: 0;
  }

  .el-dialog__body {
    padding: 0
  }

  .createPost-main-container {
    padding: 20px 0 !important;
  }

  .fitersLine {
    .el-form-item__content {
      margin-left: 0 !important
    }
    .el-form-item__label {
      min-width: auto !important;
    }
    .el-select {
      margin-top: 3px;
    }
  }
</style>



